ue\n    @media screen and (min-width: map-get($breakpoints, $name)) {\n      @content;\n    }\n  } @else {\n    // If the key doesn't exist in the map\n    @warn 'Unfortunately, no value could be retrieved from `#{$breakpoints}`. ' + 'Please make sure it is defined in `$breakpoints` map.';\n  }\n}\n\n\nbody {\n    background: #e7dada;\n    color: #3d393a;\n}\n\nh1 {\n    font-family: $sans;\n    font-weight: 300;\n    font-size: 3rem;\n\n    a {\n        text-decoration: none;\n        color: #e73f3f;\n\n        &:hover {\n            border-bottom: 2px solid #e78c8c;\n        }\n    }\n}\n\n// ---- Form basic styles.\n\nbutton {\n    padding: 9px 12px;\n    appearance: none;\n    font-family: $sans;\n    text-transform: uppercase;\n    font-weight: 900;\n    font-size: 35px;\n    color: #ffb2b2;\n    border-radius: 6px;\n    background: #3d393a;\n    text-shadow: 0 1px 1px #7f7f7f;\n    border: 0;\n    outline: 0;\n    margin-bottom: 1rem;\n\n    &:hover {\n      color: #e78c8c;\n      text-shadow: 0 1px 1px #191919;\n      cursor: pointer;\n    }\n\n}\n\n label {\n    font-family: $sans;\n    font-weight: 300;\n    font-size: 1.3rem;\n}\n\ninput[type=\"text\"],\ninput[type=\"number\"] {\n    font-family: $sans;\n    font-size: 1.25rem;\n    text-align: center;\n    display: block;\n    height: 30px;\n    line-height: 1.5;\n    padding: 8px 10px;\n    width: 300px;\n    margin: 0 auto 1.5rem;\n    // border-radius: 9px;\n    // border-color: #3d393a;\n\n    background: none;\n    border: 0;\n    outline: 0;\n    border-bottom: 2px solid #e78c8c;\n    color: #3d3a39;\n\n    &:focus {\n        border-bottom: 2px solid #ff2222;\n    }\n\n    &:-internal-autofill-selected {\n        background: transparent;\n    }\n}\n\n// Flag form -----------------\n\n.flag-form {\n    max-width: 800px;\n    margin: 0 auto;\n    text-align: center;\n    @include respond-to(afterMobile) {\n        display: flex;\n        flex-wrap: wrap;\n        align-items: center;\n        justify-content: space-around;\n    }\n    @include respond-to(desktop) {\n        max-width: 1024px;\n        align-items: start;\n    }\n}\n\n.flag-form__random-container {\n    @include respond-to(afterMobile) {\n        order: 1;\n        flex: auto;\n    }\n    @include respond-to(desktop) {\n        order: 2;\n    }\n}\n\n.flag-form__form {\n    margin-bottom: 2.3rem;\n    @include respond-to(afterMobile) {\n        order: 2;\n        flex: auto;\n    }\n    @include respond-to(desktop) {\n        order: 3;\n    }\n}\n\n.flag-form__flag-container {\n    @include respond-to(afterMobile) {\n        order: 3;\n        flex: auto;\n    }\n    @include respond-to(desktop) {\n        max-width: 600px;\n        order: 1;\n    }\n}\n\n// Flag -------------------------\n.flag__canvas-container {\n    margin-bottom: 2.3rem;\n\n    canvas {\n        max-width: 100%;\n    }\n\n}\n\n.flag__seed {\n    font-family: $sans;\n    font-size: 0.8rem;\n    color: #92586e;\n}\n\n.flag__attributes {\n    font-family: $sans;\n    display: block;\n    max-width: 590;\n    margin: 0 auto;\n}\n\n.flag__attributes-list {\n    text-align: left;\n}\n\n.flag__attributes-toggle {\n    text-transform: lowercase;\n    font-size: 1.3rem;\n    text-shadow: none;\n}\n\n// author ------\n.author {\n    @include respond-to(afterMobile) {\n        flex: 1 1 100%;\n    }\n}\n"],"sourceRoot":""}]);
